import React from 'react'
import { geoAlbersUsa, geoPath } from 'd3-geo'
import { feature, mesh } from 'topojson'
import debounce from 'lodash/debounce'
import {modes} from './App';

import './Map.css'
import MapIcon from './MapIcon'
import AutoplayQuestionTooltip from './AutoplayQuestionTooltip';

import config from './config'

import us from './us-states-topo.json'

const targetMapWidth = 960
const targetMapHeight = 500;
const mapAspectRatio = targetMapWidth / targetMapHeight;

const stateFills = feature(us, us.objects['us-states']).features
const stateOutlines = mesh(us, us.objects['us-states'], function(a, b) { return a !== b; })

const projection = geoAlbersUsa()
const defaultScale = projection.scale();
const pathGen = geoPath(projection);

export default class Map extends React.Component {
  constructor(props) {
    super(props)

    const mapWidth = window.innerWidth * 0.6;
    this.state = {
      mapWidth,
    }

    this._debouncedResize = debounce(this._resize, 100).bind(this)
  }

  componentDidMount() {
    window.addEventListener('resize', this._debouncedResize)
    this._resize()
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this._debouncedResize)
  }

  _resize = () => {
    this.setState({ mapWidth: window.innerWidth * 0.6 }, () => {
      const yOffset = this._svg.getBoundingClientRect().top - this._mapContainer.getBoundingClientRect().top
      this.setState({
        yOffset,
      })
    })
  }

  render() {
    const mulitplier = this.state.mapWidth / targetMapWidth;
    const scale = mulitplier * defaultScale
    const mapHeight = this.state.mapWidth / mapAspectRatio;

    projection.scale(scale)
      .translate([targetMapWidth * mulitplier / 2, targetMapHeight * mulitplier / 2])
    let selectedName = null
    let selectedPosition = null;
    const cities = this.props.cities.slice()
    cities.sort((a, b) => {
      const aSelected = (a.index === this.props.selectedCityIndex && this.props.nextSelectedCityIndex == null) || a.index === this.props.nextSelectedCityIndex;
      const bSelected = (b.index === this.props.selectedCityIndex && this.props.nextSelectedCityIndex == null) || b.index === this.props.nextSelectedCityIndex;
      if (aSelected && bSelected) {
        return 0
      } else if (aSelected) {
        return 1
      } else if (bSelected) {
        return -1
      } else {
        return 0
      }
    })
    const icons = cities.map((city) => {
      const selected = (city.index === this.props.selectedCityIndex && this.props.nextSelectedCityIndex == null) || city.index === this.props.nextSelectedCityIndex;
      const [x, y] = projection([city.Long, city.Lat])
      if (selected) {
        selectedName = city.City
        selectedPosition = [x, y]
      }
      return (
        <g key={city.City} transform={`translate(${x}, ${y})`} onClick={this.props.selectCity(city.index)} >
          <MapIcon fill={config.selectedColor} targetHeight={selected ? 50 * mulitplier : 30 * mulitplier} />
        </g>
      )
    })
    const selectedStateName = selectedName.split(',')[1].trim()

    let autoplayQuestionTooltip = null
    if (this.props.mode === modes.Autoplay && this.props.question) {
      autoplayQuestionTooltip = <AutoplayQuestionTooltip {...this.props} {...this.state} projection={projection} />
    }
    let cityViewQuestionTooltip = null
    if (this.props.mode === modes.CityView) {
      const cornerSize = window.innerWidth * 0.001 * 0.6;

      const x = selectedPosition[0] - cornerSize
      const y = selectedPosition[1] + this.state.yOffset - cornerSize
      const style = { left: x, top: y, color: config.selectedColor };
      if (x > this.state.mapWidth / 2) {
        style.transform = 'translateX(-100%)'
      }
      cityViewQuestionTooltip = (
        <div className='cityViewTooltip' style={style}>
          {selectedName}
        </div>
      )
    }

    return (
      <div className='map' ref={c => (this._mapContainer = c)}>
        <svg width={this.state.mapWidth} height={mapHeight} ref={c => (this._svg = c)}>
          {stateFills.map((state, i) => {
            const stateSelected = state.properties.name === selectedStateName
            const fill = stateSelected ? config.color : config.defaultMapColor
            return <path key={i} d={pathGen(state)} style={{ fill }} />
          })}
          <path d={pathGen(stateOutlines)} fill='none' stroke='#f5f5f5'/>
          {icons}
        </svg>
        {autoplayQuestionTooltip}
        {cityViewQuestionTooltip}
      </div>
    )
  }
}