import React from 'react'
import {modes} from './App';
import config from './config'
import { Trail } from 'react-spring/renderprops'
import './CityViewQuestions.css';

const defaultOffscreen = 110;
export default class CityViewQuestions extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      x: defaultOffscreen,
    }
  }
  componentDidMount() {
    if (this.props.mode === modes.CityView) {
      this.setState({ x: 0 })
    }
  }
  componentDidUpdate(prevProps) {
    if (this.props.mode === modes.CityView && prevProps.mode !== modes.CityView) {
      this.setState({ x: 0 })
    } else if (prevProps.mode === modes.CityView && this.props.mode !== modes.CityView) {
      this.setState({ x: defaultOffscreen })
    }
  }
  render() {
    const selectedCity = this.props.cities[this.props.selectedCityIndex].City
    const selectedTopic = this.props.topics[this.props.selectedTopicIndex].Topic;
    const questionsData = this.props.questions.filter(question => {
      return question.City === selectedCity && question.Subject === selectedTopic && question.Question !== ''
    })
    questionsData.forEach((q, i) => q.index = i);

    const x = this.props.dismissingCityQuestions || this.props.mode !== modes.CityView ? defaultOffscreen : this.state.x
    const questionsOffscreen = x === defaultOffscreen
    const closeButtonOpacity = questionsOffscreen ? 0 : 1
    const pointerEvents = questionsOffscreen ? 'none' : null
    const cursor = questionsOffscreen ? 'default' : null
    return (
      <div className='CityViewQuestions'>
        <div className='close' style={{ pointerEvents, color: config.color, opacity: closeButtonOpacity }}>
          <span onClick={this.props.disableCityMode} style={{ cursor }}>&times;</span>
        </div>
        <Trail
          from={{ x: defaultOffscreen }}
          to={{ x }}
          keys={question => `${selectedCity}-${question.index}`}
          items={questionsData}
          onRest={this.props.questionsRested}>
          {question => ({ x }) =>
            (
              <div className='question' style={{ backgroundColor: config.color, transform: `translateX(${x}%)` }}>
                {question.Question}
              </div>
            )
          }
        </Trail>
      </div>
    )
  }
}