import React from 'react';
import './App.css';
import DataLoader from './DataLoader';
import Menu from './Menu';
import Map from './Map'
import CityViewQuestions from './CityViewQuestions'

export const modes = {
  Autoplay: 'Autoplay',
  CityView: 'CityView',
}

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      loaded: false,
      selectedTopicIndex: 0,
      selectedCityIndex: 0,
      nextSelectedCityIndex: null,
      nextSelectedTopicIndex: null,
      mode: null,
      dismissingCityQuestions: false,
    }
    this._onRestCallbacks = []
  }
  componentDidMount() {
    DataLoader().then(({cities, topics, questions}) => {
      this.setState({
        loaded: true,
        cities,
        topics,
        questions,
        mode: modes.Autoplay,
      }, this._initAutoplay)

    })
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.mode === modes.CityViewQuestions && this.state.mode === modes.Autoplay) {
      this._initAutoplay()
    } else if (prevState.mode === modes.Autoplay && this.state.mode === modes.CityViewQuestions) {
      clearTimeout(this._autoplayTimeout);
    }
  }

  _initAutoplay = () => {
    this._autoplayTimeout = setTimeout(this._pickAutoplayQuestion, 250)
  }

  _pickAutoplayQuestion = () => {
    let question = null;
    let cityIndex = null;
    let tries = 0;
    while(question == null && tries < 20) {
      const randomCityIndex = Math.floor(Math.random() * this.state.cities.length)
      if (randomCityIndex === this.state.selectedCityIndex) {
        continue
      }
      tries ++

      const selectedCity = this.state.cities[randomCityIndex].City
      const selectedTopic = this.state.topics[this.state.selectedTopicIndex].Topic;
      const questionsData = this.state.questions.filter(question => {
        return question.City === selectedCity && question.Subject === selectedTopic && question.Question !== ''
      })
      if (questionsData.length > 0) {
        question = questionsData[Math.floor(Math.random() * questionsData.length)]
        cityIndex = randomCityIndex
      }
    }
    if (question == null) {
      console.warn('no question found, something went wrong')
    } else {
      this.setState({ selectedCityIndex: cityIndex, question })
    }
  }

  _autoplayTypingDone = () => {
    this._autoplayTimeout = setTimeout(() => {
      this.setState({ question: null }, () => {
        this._autoplayTimeout = setTimeout(this._pickAutoplayQuestion, 200)
      })
    }, 2000)
  }

  _selectTopic = (selectedTopicIndex) => {
    return () => {
      if (this.state.selectedTopicIndex === selectedTopicIndex) {
        return
      }
      if (this.state.mode === modes.CityView) {
        this.setState({
          dismissingCityQuestions: true,
          nextSelectedTopicIndex: selectedTopicIndex,
        }, () => {
          this._onRestCallbacks.push(() => {
            this.setState({ dismissingCityQuestions: false, selectedTopicIndex, nextSelectedTopicIndex: null})
          })
        })
      } else {
        this.setState({ selectedTopicIndex, question: null })
        clearTimeout(this._autoplayTimeout)
        this._initAutoplay()
      }
    }
  };

  _selectCity = (selectedCityIndex) => {
    return () => {
      if (this.state.mode === modes.CityView) {
        if (this.state.selectedCityIndex === selectedCityIndex) {
          return
        }
        this.setState({
          dismissingCityQuestions: true,
          nextSelectedCityIndex: selectedCityIndex,
        }, () => {
          this._onRestCallbacks.push(() => {
            this.setState({ dismissingCityQuestions: false, selectedCityIndex, nextSelectedCityIndex: null})
          })
        })
      } else {
        this._onRestCallbacks.length = 0
        clearTimeout(this._autoplayTimeout)
        this.setState({ selectedCityIndex, nextSelectedCityIndex: null, mode: modes.CityView, question: null })
      }
    }
  }

  _questionsRested = () => {
    this._onRestCallbacks.forEach(cb => cb())
    this._onRestCallbacks.length = 0;
  }

  _disableCityMode = () => {
    this._onRestCallbacks.length = 0
    this.setState({ mode: modes.Autoplay }, this._pickAutoplayQuestion)
  }

  render() {
    if (!this.state.loaded) {
      return null;
    }
    return (
      <div className="App">
        <Menu {...this.state} selectTopic={this._selectTopic} />
        <Map {...this.state} selectCity={this._selectCity} onTypingDone={this._autoplayTypingDone} />
        <CityViewQuestions {...this.state} questionsRested={this._questionsRested} disableCityMode={this._disableCityMode} />
      </div>
    );
  }
}

export default App;
