import { csv } from 'd3-request'
import { queue } from 'd3-queue'

const files = ['Topics.csv', 'Cities.csv', 'Questions.csv']
export default function load() {
  return new Promise((resolve, reject) => {
    queue()
      .defer(csv, `${process.env.PUBLIC_URL}/${files[0]}`)
      .defer(csv, `${process.env.PUBLIC_URL}/${files[1]}`)
      .defer(csv, `${process.env.PUBLIC_URL}/${files[2]}`)
      .await((error, topics, cities, questions) => {
        if (error) reject(error);
        cities.forEach((city, cityIndex) => {
          city.index = cityIndex
        })
        resolve({
          topics, cities, questions
        })
      })

  })
}
