import React from 'react'
import Typist from 'react-typist'
import './AutoplayQuestionTooltip.css';
import config from './config'

export default class AutoplayQuestionTooltip extends React.Component {


  render() {
    const city = this.props.cities[this.props.selectedCityIndex]
    const [x, yProjected] = this.props.projection([city.Long, city.Lat])

    const y = yProjected + this.props.yOffset;

    // let's not flip anything for now
    // const translateX = '0'
    // const translateY = y > window.innerHeight * 0.5 ? '-100%' : '0'
    const cornerSize = window.innerWidth * 0.001 * 0.6;
    const style = {
      left: x - cornerSize, top: y - cornerSize,
      // transform: `translate(${translateX}, ${translateY})`
    }

    return (
      <div className='AutoplayQuestionTooltip' style={style}>
        <div className='cityName' style={{ color: config.selectedColor }}>{city.City}</div>
        <div className='question'>
          <Typist startDelay={500} onTypingDone={this.props.onTypingDone}>{this.props.question.Question}</Typist>
        </div>
      </div>
    )
  }
}
