import React from 'react';
import config from './config';
import './Menu.css';

export default function Menu(props) {
  const coloredFontStyle = { color: config.color }
  const topics = props.topics.map((topic, topicIndex) => {
    const selected = (topicIndex === props.selectedTopicIndex && props.nextSelectedTopicIndex == null) || topicIndex === props.nextSelectedTopicIndex ;
    const style = { ...coloredFontStyle, borderColor: selected ? config.selectedColor : null }
    return <li key={topic.Topic} style={style} onClick={props.selectTopic(topicIndex)}>{topic.Topic}</li>
  })
  return (
    <div className='menu'>
      <div className='menuWrapper'>
        <img src={`${process.env.PUBLIC_URL}/grow-with-google-logo.svg`} alt='Grow with Google' />
        <div className='text'>
          <div className='title' style={coloredFontStyle}>{config.title}</div>
          <div className='subtitle'>{config.subtitle}</div>
          <div className='instructions' style={coloredFontStyle}>{config.instructions}</div>
        </div>

        <ul>
          {topics}
        </ul>
      </div>
    </div>
  )
}
